<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">调仓移库</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}调仓移库单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" ref="TableDataRef" :model="params" class="demo-form-inline">
        <el-form-item label="调出组织" size="mini" prop="company_id" :rules="rules.company_id">
          <el-select clearable v-model="params.company_id" placeholder="请选择" filterable @focus="companyListInvok" @change="companChange">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单据号" size="mini">
          <el-input v-model="params.receipt_sn" disabled></el-input>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker v-model="params.business_time" type="date" placeholder="选择日期" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="调出部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @focus="DepartmentListInvok">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="调入组织" size="mini" prop="new_company_id" :rules="rules.company_id">
          {{(function(){if(params.new_company_id == 0)params.new_company_id = null})()}}
          <el-select clearable v-model="params.new_company_id" placeholder="请选择" filterable @change="newCompanyChange" @focus="companyListInvok">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="调入仓库" size="mini">
          {{(function(){if(params.warehouse_id == 0)params.warehouse_id = null})()}}
          <el-select clearable v-model="params.warehouse_id" placeholder="请选择" @focus="warehouseFocus" filterable @change="warehouseChange">
            <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="调入货位" size="mini">
          {{(function(){if(params.allocation_id == 0)params.allocation_id = null})()}}
          <el-select clearable v-model="params.allocation_id" @focus="allocationFocus" placeholder="请选择" filterable size="mini" @change="allocationChange">
            <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in allocationInventoryList"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="delF" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <xTable ref='xTable' :tableColumn='tableColumnAdd' :totalArray="['new_goods_num']" :isEdit='isEdit'></xTable>
    </div>
    <el-form ref="form" :model="form" label-width="80px" class="footerRemark">
      <el-form-item label="备注:">
        <el-input type="text" maxlength="300" width='600' v-model="params.remark" placeholder="请输入"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack()">取 消</el-button>
      <el-button type="primary" @click="submit('TableDataRef')">确 定</el-button>
    </div>

    <!-- 选择商品 -->
    <goodsPopout v-if='dialogFormVisible' :defaultNum="20" @close='dialogFormVisible = false' :ExcludeTop="['brand_name','unit_name','factory_code','specification_type','pattern']"   :ExcloudParams="['pattern','specification_type','shopType']"  :searchParams='{company_id:params.company_id}'  @submit="closeSubmit" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { opthionList, repertory } from "common/utils/public_function/commonUtils";
import {
  TableHead,
  submitRowModule,
  rowModule,
} from "./config/transferwareAdd";
export default {
  name: "instorageTransferwareTransferwareAdd",
  mixins: [TableHead],
  data() {
    return {
      title: "新增",
      addChandise: false,
      loading:false,
      dialogFormVisible: false,
      state: false, //改变cheackbox状态
      flag: true,
      indexchart: {},
    };
  },
  computed: {
    ...mapGetters(["transferwareAddGet"]),
  },
  methods: {
    ...mapActions(["transferwareIndexACT"]),
    companChange() {
      this.$refs.xTable._proxy("loadData", []);
    },
    goBack() {
      this.$router.push({
        path: "/inventory/transferware",
      });
      this.transferwareIndexACT({
        goPage: this.transferwareAddGet.goPage,
        params: this.transferwareAddGet.params,
        pageSize: this.transferwareAddGet.pageSize,
      });
    },
    warehouseChange(val) {
      if (val) {
        this.params.warehouse_name = this.warehouseList.filter(
          (obj) => obj.warehouse_id === val
        )[0].warehouse_name;
        let result = this.$refs.xTable
          ._proxy("getTableData")
          .fullData.concat([]);
        if (result.length > 0) {
          result.forEach((item) => {
            item.new_warehouse_id = val;
            item.new_warehouse_name = this.params.warehouse_name;
          });
          this.$refs.xTable._proxy("loadData", result);
        }
      }
    },
    allocationChange(val) {
      if (val) {
        this.params.allocation_name = this.allocationInventoryList.filter(
          (obj) => obj.allocation_id === val
        )[0].allocation_name;
        let result = this.$refs.xTable
          ._proxy("getTableData")
          .fullData.concat([]);
        if (result.length > 0) {
          result.forEach((item) => {
            item.new_allocation_id = val;
            item.new_allocation_name = this.params.allocation_name;
          });
          this.$refs.xTable._proxy("loadData", result);
        }
      }
    },
    // 逻辑计算
    logicCount(row, countType) {
      let _num = Number(row.new_goods_num),
        _price = Number(row.cost_price), // 单价
        _total_price = Number(row.subtotal_cost_price); // 金额
      switch (countType) {
        case 1: // 乘
          if (!isNaN(_num) && !isNaN(_price)) {
            let total_price = _num * _price;
            this.$set(row, "subtotal_cost_price", total_price.toFixed(2));
          }
          break;
        case 2: // 除
          if (!isNaN(_total_price) && _num != 0) {
            let price = _total_price / _num;
            this.$set(row, "cost_price", price.toFixed(2));
          }
          break;
      }
      this.$nextTick(() => {
        this.$refs.xTable._updateFooter();
      });
    },
    closeSubmit(val) {
      this.dialogFormVisible = false;
      let valsub = val.map((val) => {
        val.new_goods_num = val.goods_num;
        val.subtotal_cost_price =
          Number(val.new_goods_num) * Number(val.cost_price);
        val.goods_num = val.stocks;
        return val;
      });
      this.$refs.xTable._proxy("insertAt", valsub, this.indexchart);
      this.$nextTick(() => {
        this.$refs.xTable._updateFooter();
      });
    },
    allocationFocus() {
      if (this.params.warehouse_id) {
        this.loading = true;
        this.$axMethod
          .allocationInventoryList({
            warehouse_id: this.params.warehouse_id,
            company_id: this.params.new_company_id,
          })
          .then(({ data }) => {
            if (data.code == "200") {
              this.allocationInventoryList = data.data;
            }
            this.loading = false;
          })
          .catch(() => {
            this.loading = false;
          });
      }
    },
    warehouseFocus() {
      this.$axMethod
        .getWarehouseFocus({
          company_id: this.params.new_company_id
            ? this.params.new_company_id
            : "",
          type: 1,
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$set(this, "warehouseList", data.data);
          }
        });
    },
    // el-input的 input 事件
    elInput(row, field, inputType) {
      /**
       * row 编辑行
       * field 编辑字段
       * inputType 计算类型 1 乘(调仓数量*调仓单价) 2 除(调仓金额/调仓数量)
       */
      if (!isNaN(Number(row[field]))) {
        if (field) {
          let value = row[field].replace(/^(\-)*(\d+)\.(\d\d).*$/, "$1$2.$3");
          this.$set(row, field, value);
        }
        this.logicCount(row, inputType);
      } else {
        this.$set(row, field, null);
      }
    },
    // 公司改变
    newCompanyChange() {
      this.$set(this.params, "warehouse_id", "");
      this.$set(this.params, "allocation_id", "");
      var box = this.$refs.xTable._proxy("getTableData").fullData.concat([]);
      box.forEach((obj) => {
        obj.new_warehouse_id = null;
        obj.new_allocation_id = null;
        obj.new_warehouse_name = null;
        obj.new_allocation_name = null;
      });
      this.$refs.xTable._proxy("loadData", box);
    },
    //  打开选择商品得弹框
    goSelectShop(scope) {
      if (this.params.company_id && this.params.new_company_id) {
        this.dialogFormVisible = true;
        this.indexchart = scope.row;
      } else {
        this.$message({
          message: "请先选择调入调出组织！",
          type: "warning",
        });
      }
      this.flag = false;
    },
    changeGoods(value, scope) {
      var hasEmptyData = true;
      const row = scope.row;
      let changeID = ["goods_id", "goods_name", "unit_name"];
      let autofields = {};
      for (var i in rowModule) {
        if (changeID.includes(i)) {
          autofields[i] = value[i];
        } else {
          autofields[i] = rowModule[i];
        }
      }
      autofields.goods_num = value.stocks;
      Object.assign(row, autofields);
      this.$refs.xTable._proxy("getTableData").fullData.forEach((item) => {
        if (!item.goods_id) hasEmptyData = false;
      });
      if (hasEmptyData) {
        this.addF();
      }
    },
    addF() {
      if (this.params.company_id && this.params.new_company_id) {
        this.$refs.xTable._proxy(
          "insertAt",
          this.$publicFution.deepCopy(
            Object.assign(rowModule, {
              new_warehouse_id: this.params.warehouse_id,
              new_warehouse_name: this.params.warehouse_name,
              new_allocation_id: this.params.allocation_id,
              new_allocation_name: this.params.allocation_name,
            })
          ),
          -1
        );
        this.$refs.xTable._updateFooter();
      } else {
        this.$message.warning("请先选择调入调出组织!");
      }
    },
    submit(formName) {
      let vm = this;
      let data = this.$refs.xTable
        ._proxy("getTableData")
        .fullData.concat([])
        .filter((val) => {
          return val.goods_id;
        });
      if (!data.length) {
        this.$message.warning("请先添加商品！");
        return false;
      }
      try {
        let _this = this;
        data.forEach((item, i) => {
          ["new_warehouse_id", "new_allocation_id", "new_goods_num"].forEach(
            (val, index) => {
              if (!item[val]) {
                let setArry = [
                  "new_warehouse_name",
                  "new_allocation_name",
                  "new_goods_num",
                ];
                this.$refs.xTable._proxy("setActiveCell", item, setArry[index]);
                throw Error(false);
              }
            }
          );
          if (
            item.stocks <= 0 ||
            item.goods_num < item.new_goods_num ||
            item.stocks < item.new_goods_num
          ) {
            _this.$message.warning(
              `新增调仓移库单失败。第${i + 1}条商品在库数量不足`
            );
            this.$refs.xTable._proxy("setActiveCell", item, "new_goods_num");
            throw Error(false);
          }
          if (item.available_stocks <= 0) {
            _this.$message.warning(
              `新增调仓移库单失败。第${i + 1}行批次的商品可用库存量不足`
            );
            this.$refs.xTable._proxy("setActiveCell", item, "new_goods_num");
            throw Error(false);
          }
        });
      } catch (error) {
        return false;
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var params = this.params;
          params.goods_info = data.map((item) => {
            return this.$publicFution.filter(submitRowModule, item);
          });
          let api = "addTransferBills";
          if (this.transferwareAddGet.transfer_id) {
            // 调仓移库修改
            params.transfer_id = this.transferwareAddGet.transfer_id;
            api = "editTransferBills";
          }
          // 校验安全库存金额参数
          var box = data.map((item) => {
            return {
              warehouse_id: item.warehouse_id,
              warehouse_name: item.warehouse_name,
              allocation_id: item.allocation_id,
              goods_id: item.goods_id,
              batch: item.batch,
              order_goods_id: item.order_goods_id || 0,
              sale_price: item.cost_price || 0,
              batch_id: item.batch_id,
              goods_num: item.new_goods_num,
            };
          });
          // 校验安全库存金额 风控
          this.$baseData
            .checkComputation(data, {
              goods_num: "new_goods_num",
              total_price: "subtotal_cost_price",
              goods_price: "cost_price",
            })
            .then(() => {
              this.$axMethod
                .safetyStockCheck({
                  bill_sn: this.params.transfer_sn,
                  bill_id: this.params.transfer_id,
                  company_id: this.params.company_id,
                  company_name: this.params.company_name,
                  business_time: this.params.business_time,
                  bill_logogram: "tcyk",
                  order_type: 1,
                  goods_info: JSON.stringify(box),
                })
                .then((result) => {
                  params.goods_info = data.map((item) => {
                    return this.$publicFution.filter(submitRowModule, item);
                  });
                  params.goods_info = JSON.stringify(params.goods_info);
                  repertory(
                    this,
                    {
                      company_id: this.params.company_id,
                      goods_info: params.goods_info,
                      status: 1,
                      type: 5,
                    },
                    function () {
                      vm.$axMethod[api](params).then(({ data }) => {
                        if (data.code == "200") {
                          vm.$message({
                            message: data.msg,
                            type: "success",
                          });
                          vm.goBack();
                        }
                      });
                    }
                  );
                });
            });
        }
      });
    },
  },
  mounted() {
    this.title = this.transferwareAddGet.transfer_id ? "修改" : "新增";
    if (this.transferwareAddGet.transfer_id) {
      this.params.transfer_id = this.transferwareAddGet.transfer_id;
      this.$axMethod.infoTransferBills(this.params).then(({ data }) => {
        if (data.code == "200") {
          this.$set(this, "params", data.data.info);
          this.$refs.xTable._proxy("loadData", data.data.goods_list);
          opthionList(this, data.data.info);
          this.companyList.push({
            company_id: data.data.info.new_company_id,
            company_name: data.data.info.new_company_name,
          });
        }
      });
    } else {
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        new_company_id: Number(localStorage.company_id),
        business_time: this.$publicFution.formatData(new Date()),
      });
      opthionList(this, window.localStorage);
      this.addF();
    }
  },
};
</script>
<style lang="less">
.footerRemark {
  .el-form-item__content .el-input {
    width: 100%;
  }
}
</style>
