// 提交模板
export const submitRowModule = {
  "goods_id": '',
  "batch_id": '',
  "batch": '',
  "allocation_id": '',
  "warehouse_id": '',
  "transfer_warehouse_id": 0,
  "goods_num": '',
  "new_warehouse_id": '',
  "new_allocation_id": '',
  "new_goods_num": '',
  "cost_price": '',
  "subtotal_cost_price": '',
  "remark": ''
}
// 模板
export const rowModule = {
  "goods_id": '',
  "batch_id": '',
  "batch": '',
  "allocation_id": '',
  "warehouse_id": '',
  "goods_num": '',
  "new_warehouse_id": '',
  "new_allocation_id": '',
  "allocation_name": '',
  "warehouse_name": '',
  "new_warehouse_name": '',
  "new_allocation_name": '',
  "new_goods_num": 0,
  "goods_name": '',
  "cost_price": 0,
  "subtotal_cost_price": 0,
  'remark': ''
}
// 提交模板
// export const sumbitRowModule = {
// }
export const TableHead = {// 需要传进来一个this对象
  data() {
    return {
      tableColumnAdd: [
        {
          type: "checkbox",
          fixed: 'left',
          width: 50,
          showOverflow: true,
        },
        {
          type: 'index',
          title: '序号',
          width: 50,
          showOverflow: true
        },
        {
          title: '商品编号',
          field: 'goods_id',
          width: 100,
          showOverflow: true
        },
        {
          title: '商品名称',
          field: 'goods_name',
          width: 400,
          showOverflow: true,
          editRender: {
            name: 'vGoods',
            getParams: (scope) => {
              let _this = this
              return {
                params: {
                  company_id: this.params.company_id || 0,
                  customer_id: this.params.customer_id || 0,
                  url: 'getGoodsSnList',
                  // warehouse_id: this.params.warehouse_id || 0,// 选择商品不带仓库
                  bill_type: 2
                },
                getIcon() { // 渲染器内部图标 返回一个数组 (非必传 默认为空)
                  return [{
                    icon: 'el-icon-plus', // 渲染器图标 基于element图标 (非必传 默认为查询图标)
                    iconMethod() { // 渲染器图标点击方法 (非必传)
                      _this.goSelectShop(scope)
                    }
                  }]
                },
              }
            },
            events: {
              change: this.changeGoods
            }
          }
        },
        {
          title: '年周/批次',
          field: 'batch',
          width: 120,
          editRender: {
            name: 'vBatch',
            getParams: ({ row }) => {
              return {
                url: 'getGoodsBatchList',
                params: {
                  company_id: this.params.company_id,
                  goods_id: row.goods_id ? row.goods_id : ""
                },
                showObj: {
                  batch: "年周/批次",
                  warehouse_name: "仓库名称",
                  allocation_name: "货位名称",
                  available_stocks: "批次可用库存",
                  stocks: "在库库存"
                }
              }
            },
            events: {
              change: (val, { row }) => {
                [
                  'batch',
                  'stocks',
                  "batch_id",
                  "production_time",
                  "allocation_id",
                  "cost_price",
                  "allocation_name",
                  "warehouse_name",
                  "warehouse_id"
                ].forEach(key => {
                  row[key] = val[key]
                });
                this.elInput(row, 'cost_price', 1)
              }
            }
          }
        },
        {
          field: 'unit_name',
          title: '单位',
          showOverflow: true,
          width: 110
        },
        {
          title: '调出仓库',
          field: 'warehouse_name',
          width: 240,
          showOverflow: true
        },
        {
          title: '调出货位',
          field: 'allocation_name',
          width: 130,
          showOverflow: true
        },
        {
          title: '调入仓库',
          field: 'new_warehouse_name',
          width: 150,
          showOverflow: true,
          editRender: {
            name: 'vSelect',
            getParams: ({
              row
            }) => {
              return {
                url: 'getWarehouseFocus',
                modelKey: 'new_warehouse_id',
                params: {
                  company_id: this.params.new_company_id
                },
                itemObj: {
                  id: 'warehouse_id',
                  name: 'warehouse_name'
                }
              }
            },
            events: {
              change: ({
                row
              }, obj) => {
                row.new_warehouse_id = obj.warehouse_id
                row.new_warehouse_name = obj.warehouse_name
                row.new_allocation_id = ''
                row.new_allocation_name = ''
                this.$axMethod.allocationInventoryListFocus({
                  warehouse_id: obj.warehouse_id
                }).then(({
                  data
                }) => {
                  let allocation = data.data.filter(val => {
                    return val.is_system == 1
                  })[0];
                  this.$set(row, 'new_allocation_id', allocation.allocation_id)
                  this.$set(row, 'new_allocation_name', allocation.allocation_name)
                })
              }
            },
          }
        },
        {
          title: '调入货位',
          field: 'new_allocation_name',
          width: 150,
          showOverflow: true,
          editRender: {
            name: 'vAllocation',
            getParams: ({
              row
            }) => {
              return {
                url: 'allocationInventoryList',
                modelKey: 'new_allocation_id',
                modelLabel: 'new_allocation_name',
                params: {
                  company_id: this.params.new_company_id,
                  warehouse_id: row.new_warehouse_id
                },
                showObj: {
                  allocation_name: ''
                }
              }
            },
            events: {
              change: ({ row }, val) => {
                row.new_allocation_id = val.allocation_id
                row.new_allocation_name = val.allocation_name
              }
            },
          }
        },
        {
          title: '调仓数量',
          field: 'new_goods_num',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 130,
          showOverflow: true,
          editRender: {
            name: 'number',
            events: {
              input: ({
                row
              }) => {
                this.elInput(row, 'new_goods_num', 1)
              }
            }
          }
        },
        {
          title: '调仓单价',
          field: 'cost_price',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 130,
          showOverflow: true,
          editRender: {
            name: 'Vinput',
            events: {
              input: ({
                row
              }) => {
                this.elInput(row, 'cost_price', 1)
              }
            }
          }
        },
        {
          title: '调仓金额',
          field: 'subtotal_cost_price',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 130,
          showOverflow: true,
          editRender: {
            name: 'Vinput',
            events: {
              input: ({
                row
              }) => {
                this.elInput(row, 'subtotal_cost_price', 2)
              }
            }
          }
        },
        {
          field: 'remark',
          title: '备注',
          width: 150,
          showOverflow: true,
          editRender: {
            name: 'Vinput',
            formatter: 'String'
          }
        }
      ]
    }
  },
  methods: {
    // 是否可编辑
    isEdit({ column, row, $rowIndex }) {
      return true
    },
    // 删行
    delF() {
      if (this.$refs.xTable._proxy("getCheckboxRecords").length) {
        this.$refs.xTable._proxy("removeSelecteds");
      } else {
        this.$message.warning('请先选择要删除的订单！');
      }
    }
  }
}